import React, { Component } from "react";
import "./App.css";
import parseXLS from "./parser";
import { saveAs } from "file-saver";

class App extends Component {
  constructor() {
    super();
    this.state = {
      files: [],
      error: ""
    };
    this.convertFilesToJson = this.convertFilesToJson.bind(this);
  }
  async convertFilesToJson() {
    const { files } = this.state;
    if (files.length > 1) {
      this.setState({ error: "Uploadujte samo jedan fajl!" });
      return;
    }
    if (files[0].type !== "application/vnd.ms-excel") {
      this.setState({ error: "Niste uploadovali .xls ili .xlsx fajl!" });
      return;
    }
    try {
      const data = await parseXLS(files, { camelCase: true });
      const blob = new Blob([JSON.stringify(data, null, 2)], {
        type: "text/plain;charset=utf-8"
      });
      const fileName = files[0].name.split(".xls")[0] + ".json";
      saveAs(blob, fileName);
    } catch (error) {
      this.setState({ error });
    }
  }
  render() {
    const { files, error } = this.state;
    return (
      <div className="App">
        <header className="App-header">
          {error ? (
            <div>
              <p>{error}</p>
              <button onClick={() => this.setState({ files: [], error: "" })}>
                Nazad
              </button>
            </div>
          ) : (
            <p>
              Izaberite <code>.xls</code> ili <code>.xlsx</code> fajl za
              konverziju.
            </p>
          )}
          {files.length ? (
            <div>
              <button onClick={this.convertFilesToJson}>Konvertuj</button>
              <button onClick={() => this.setState({ files: [] })}>
                Nazad
              </button>
            </div>
          ) : (
            <input
              type="file"
              name="file"
              onChange={e => {
                e.preventDefault();
                const { files } = e.target;
                if (files.length > 1) {
                  this.setState({ error: "Uploadujte samo jedan fajl!" });
                } else if (files[0].type !== "application/vnd.ms-excel") {
                  this.setState({
                    error: "Niste uploadovali .xls ili .xlsx fajl!"
                  });
                } else {
                  this.setState({ files: Array.from(e.target.files) });
                }
              }}
            />
          )}
          <ul>
            {console.log(files)}
            {files &&
              files.map((file, i) => (
                <li key={i}>
                  <strong>{file.name}</strong>
                  <small>
                    {parseFloat(file.size / (100 * 1000)).toFixed(2)} MB
                  </small>
                </li>
              ))}
          </ul>
        </header>
      </div>
    );
  }
}

export default App;
